.main{
  padding: 20px 20px;
  border: 2px solid black;
  border-radius: 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
}

.heading{
  margin-bottom: 10px;
  font-size: 25px;
}

.button{
  margin-top: 10px;
  padding: 5px 20px;
  background-color: transparent;
  border-style: none;
  border: 1px solid;
  border-radius: 10px;
}

.input{
  padding: 8px 5px;
  border-radius: 10px;
  border-style: none;
  background-color: transparent;
}